import React,{ useState, useEffect } from 'react';
import logo from './LoadingIcon.png';
import './App.css';

function App() {

  const [response, setResponse] = useState()
  
  let location = window.location

  useEffect(()=>{
    console.log(location)
    requestAPI(location.pathname,location.search).then(res => setResponse(res))
  }, [location])


  const renderResponse = () => {

  }

  if(!response)
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
      </header>
    </div>
  );
  else return (
    <div>
      {renderResponse()}
    </div>
  )
}

function requestAPI(pathname,search) {
  const URL = process.env.REACT_APP_API_URL || "http://146.193.226.39:8080"
  var requestURL = `${URL}${pathname}${search}`

  console.log(requestURL)
  return fetch(requestURL).then(response => response.json())
}

export default App;

